export default {
	name: 'navTab',
	props: {
		tabTitle: {
			type: Array,
			default: []
		}

	},
	data() {
		return {
			tabClick: 0, //导航栏被点击
			isLeft: 0, //导航栏下划线位置
			isWidth: 0, //每个导航栏占位
			toView:'',
		};
	},
	created() {
		var that = this
		// 获取设备宽度
		uni.getSystemInfo({
			success(e) {
				that.isWidth = e.windowWidth / that.tabTitle.length //宽度除以导航标题个数=一个导航所占宽度
			}
		})
		this.toView = 'id0'
	},
	methods: {
		// 导航栏点击
		navClick(index) {
			// this.$parent.currentTab = index 
			this.$emit('changeTab', index);//设置swiper的第几页
			this.tabClick = index //设置导航点击了哪一个
			this.isLeft = index * this.isWidth //设置下划线位置
		}
	}
}
